<template>
  <div ref="wrapper" class="scroll-wrapper">
    <slot></slot>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  props: {
    /**
     * 1 scroll
     * 2 scroll
     * 3 scrollswipescroll
     */
    probeType: {
      type: Number,
      default: 1
    },
    // click
    click: {
      type: Boolean,
      default: true
    },
    // 
    scrollX: {
      type: Boolean,
      default: false
    },
    // 
    listenScroll: {
      type: Boolean,
      default: false
    },
    // 
    scrollData: {
      type: Array,
      default: null
    },
    // 
    pullup: {
      type: Boolean,
      default: false
    },
    // 
    pulldown: {
      type: Boolean,
      default: false
    },
    // 
    beforeScroll: {
      type: Boolean,
      default: false
    },
    // scroll
    refreshDelay: {
      type: Number,
      default: 20
    }
  },
  mounted() {
    //  DOM  better-scroll
    this.$nextTick(() => {
      this.initScroll()
    })
  },
  updated() {
    this.bs.refresh()
  },
  methods: {
    initScroll() {
      // better-scroll 
      this.bs = new BScroll(this.$refs.wrapper, {
        probeType: 3,
        click: true
      })
      this.bs.on('scroll', () => {
          console.log('scrolling-')
        })
        this.bs.on('scrollEnd', () => {
          console.log('scrollingEnd')
        })
    } 
  }
}
</script>

<style lang="less" scoped>
  .scroll-wrapper {
    width: 100%;
    height: 100%;
    overflow: hidden;
    overflow-y: scroll;
    touch-action: pan-y;
  }
</style>